import E from 'wangeditor'
export default{
  data () {
    return {
      ruleValidate: {
        username: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            // type: "string",
            max: 7,
            message: "",
            trigger: "blur",
          }
        ],
        catename: [
          {
            type: "string",
            required: true,
            message: ",",
            trigger: "blur"
          },
          {
            type: "string",
            max: 7,
            message: ",",
            trigger: "blur"
          }
        ],
        email: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          { type: "email", message: "", trigger: "blur" }
        ],
        address: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        // sex: [{ required: true, message: "", trigger: "change" }],
        // age: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "blur"
        //   },
        //   {
        //     type: "number",
        //     message: "",
        //     trigger: "blur"
        //   }
        // ],
        password: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            type: "string",
            min: 6,
            message: ",",
            trigger: "blur"
          }
        ]
      },
      editor: null
    }
  },
  mounted () {
    this.editor = new E('#div1', '#div2')
    this.editor.customConfig.uploadImgServer = 'http://127.0.0.1:9000/article/articleImg'  // 
    // tab
    this.editor.customConfig.showLinkImg = false
    // console.log(this.editor.txt.html())
    this.editor.customConfig.uploadFileName = 'file'
    this.editor.customConfig.uploadImgHooks = {
       before: function (xhr, editor, files) {
        // 
        // xhr  XMLHttpRequst editor files 
        
        //  {prevent: true, msg: 'xxxx'} 
        // return {
        //     prevent: true,
        //     msg: ''
        // }
      },
      error: function(xhr, editor) {
        console.log(11)
        // 
        // xhr  XMLHttpRequst editor 
      },
      customInsert: function (insertImg, result, editor) {
          // 
          // insertImg editor result 

          //  {url:'....'} 
          // console.log()
          let url = result.url
          url = 'http://127.0.0.1:9000' + url.replace(/\\/g,'/')
          insertImg(url)
      }
    }
    this.editor.create()
  }
}